import {t} from 'sn-translate';

import {BREACHED_STAGE_CONFIG, OUT_OF_SCHEDULE_STAGE_CONFIG} from '../../constants';

import {inSchedule, inProgress, isPaused, isBreachedStage} from './stageUtils';

const getDurationUnit = (duration, secondsCount) => ({
	value: Math.floor(duration / secondsCount),
	remainder: duration % secondsCount
});

const padValue = value => `0${value}`.slice(-2);

const formatDuration = duration => {
	const years = getDurationUnit(duration, 31556952);
	const months = getDurationUnit(years.remainder, 2592000);
	if (years.value) 
		return t('{0}yr {1}mo', years.value, months.value);

	const weeks = getDurationUnit(months.remainder, 604800);
	if (months.value) 
		return t('{0}mo {1}wk', months.value, weeks.value);

	const days = getDurationUnit(weeks.remainder, 86400);
	if (weeks.value) 
		return t('{0}wk {1}d', weeks.value, days.value);

	const hours = getDurationUnit(days.remainder, 3600);
	if (days.value) 
		return t('{0}d {1}hr', days.value, hours.value);

	let minutes = getDurationUnit(hours.remainder, 60);
	if (hours.value) 
		return t('{0}hr {1}min', hours.value, minutes.value);

	const seconds = padValue(minutes.remainder);
	minutes = padValue(minutes.value);

	return `${minutes}:${seconds}`;
};

export default ({
	slaName,
	fullDuration,
	duration,
	hasBreached,
	breachedOn,
	stageDisplayValue,
	stageValue,
	schedule,
	endTime
}) => {
	const baseConfig = {
		slaName,
		displayValue: null,
		stageValue: null,
		percentage: null,
		resumes: null,
		stops: null,
		breachedOn: null,
		durationRemaining: null,
		endTime: null
	};

	if (!inProgress(stageValue) && !isBreachedStage(stageValue) && !isPaused(stageValue)) {
		return {
			...baseConfig,
			displayValue: stageDisplayValue,
			stageValue,
			endTime
		};
	}

	if (hasBreached) {
		return {
			...baseConfig,
			breachedOn,
			...BREACHED_STAGE_CONFIG
		};
	}

	const durationDisplayValue = formatDuration(duration);
	const scheduleActualStart = schedule && schedule.actual_start && schedule.actual_start.display_value;
	const scheduleActualEnd = schedule && schedule.actual_end && schedule.actual_end.display_value;

	const isInSchedule = inSchedule(schedule);

	baseConfig.percentage = Math.round(((fullDuration - duration) / fullDuration) * 100);
	baseConfig.durationRemaining = durationDisplayValue;

	if (!isInSchedule && inProgress(stageValue, hasBreached)) {
		return {
			...baseConfig,
			resumes: scheduleActualStart,
			...OUT_OF_SCHEDULE_STAGE_CONFIG
		};
	}

	return {
		...baseConfig,
		stageValue,
		displayValue: isPaused(stageValue) ? stageDisplayValue : durationDisplayValue,
		stops: isInSchedule ? scheduleActualEnd : null,
		resumes: !isInSchedule ? scheduleActualStart : null
	};
};
