/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {SLA_TIMER_REFRESH_ACTION} from '../constants';
import '../now-sla-timer-provider';

import {PROVIDER_ID} from './constants';
import {postTimerConfigToProvider} from './effects';
import view from './view';

/**
 * The SLA Timer displays a countdown for a Task SLA record,
 * matching the Task and Configuration provided. The component updates
 * on every stage change as well as when the task goes in an out of schedule.
 * 
 * Supported stages are:
 * - Paused;
 * - Breached;
 * - Out of Schedule;
 * - Completed/Achieved (Achieved for 2010 engine or property: `com.snc.sla.compatibility.breach=true`);
 * - Cancelled;
 * 
 * ```jsx
 * <now-sla-timer
 *		task="02591d81539200100999ddeeff7b1270"
 *		configuration="00e89181539200100999ddeeff7b1234"
 *		show-icon={true}
 * />
 * ```
 * 
 * @seismicElement now-sla-timer
 * @summary A countdown timer displaying information for a single Task SLA.
*/
createCustomElement('now-sla-timer', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * `sys_id` of the task for which SLA Timer will be displayed.
		 * @type {string}
		 * @uib.description sys_id of the task for which SLA Timer will be displayed.
		 */
		task: {
			default: null,
			required: true,
			schema: {
				type: 'string'
			}
		},
		/**
		 * `sys_id` of configuration record specifying which SLA will be shown.
		 * @type {string}
		 * @uib.description sys_id of configuration record specifying which SLA will be shown.
		 */
		configuration: {
			default: null,
			required: false,
			schema: {
				type: 'string'
			}
		},
		/**
		 * Property which controls whether to display the clock icon on the timer or not.
		 * @type {boolean}
		 * @uib.description Property which controls whether to display the clock icon on the timer or not.
		 */
		showIcon: {
			default: false,
			required: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * @private
		*/
		data: {
			required: false,
			default: `@${PROVIDER_ID}/slaTimerData`
		}
	},
	initialState: {
		provider: null
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: postTimerConfigToProvider,
		[SLA_TIMER_REFRESH_ACTION]: postTimerConfigToProvider
	}
});
