import {SLA_TIMER_GET_DATA_EVENT} from '../constants';

import {PROVIDER_ID} from './constants';

export const postTimerConfigToProvider = ({
	state: {
		provider,
		properties: {task, configuration, nowId}
	},
	updateState
}) => {
	let timerProvider = provider;
	if(!timerProvider) {
		timerProvider = document.getElementById(PROVIDER_ID);

		if (!timerProvider) {
			timerProvider = document.createElement('now-sla-timer-provider');
			timerProvider.setAttribute('component-id', PROVIDER_ID);
			timerProvider.id = PROVIDER_ID;
			document.body.appendChild(timerProvider);
		}
    
		updateState({provider: timerProvider});
	}
	
	// Comparison to true is needed due to browsers interpreting task attribute with no value as task=true
	if (!task || task === true) {
		console.info(`SLA Timer ${nowId} could not be rendered: task sys_ids must be supplied.`);
		return;
	}

	const event = new CustomEvent(SLA_TIMER_GET_DATA_EVENT,
		{
			bubbles: false,
			detail: {task, configuration}
		}
	);
	setTimeout(() => timerProvider.dispatchEvent(event), 0);
};
