import '../now-sla-timer-renderer';
import {DEFAULT_CONFIG_ID} from './constants';

export default ({
	properties: {
		task,
		configuration,
		showIcon,
		data
	}
}) => {
	if (!data)
		return null;

	const {sla_timers: slaTimers, config: configurations} = data;

	const configData = (configuration && configurations[configuration]) || configurations[DEFAULT_CONFIG_ID];
	const slaId = configData && task && configData[task];
	const slaTimerData = slaId && slaTimers[slaId];
  
	if(!slaTimerData)
		return null;

	return (
		<now-sla-timer-renderer showIcon={showIcon} data={slaTimerData} />
	);
};
