/* istanbul ignore file */
import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {SLA_TIMER_REFRESH_ACTION} from '../constants';

import updateTimer from './utils/updateTimer';
import cleanupTimer from './utils/cleanupTimer';

import view from './view';
import styles from './_styles.scss';

createCustomElement('now-sla-timer-renderer', {
	renderer: {type: snabbdom},
	view,
	styles,
	initialState: {
		displayValue: 'LOADING',
		stageValue: 'loading',
		counter: null,
		scheduleTimeout: null,
		targetRef: null
	},
	properties: {
		showIcon: {default: false},
		data: {default: {}}
	},
	actionHandlers: {
		[actionTypes.COMPONENT_CONNECTED]: updateTimer,
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: updateTimer,
		[actionTypes.COMPONENT_DISCONNECTED]: cleanupTimer,
	},
	dispatches: {
		[SLA_TIMER_REFRESH_ACTION]: {}
	}
});
