export default (
	slaTimerData,
	{
		sla_timers: slaTimersData,
		config: configsData
	},
	fetchedTasks
) => {
	const patchedSlaTimerData = slaTimerData ? JSON.parse(JSON.stringify(slaTimerData)) : {
		sla_timers: {},
		config: {}
	};

	// Patch in new data without destroying old one
	Object.assign(patchedSlaTimerData.sla_timers, slaTimersData);
	Object.keys(configsData).forEach(configId => {
		patchedSlaTimerData.config[configId] ?
			Object.assign(patchedSlaTimerData.config[configId], configsData[configId]) : 
			Object.assign(patchedSlaTimerData.config, {[configId]: configsData[configId]});
		
		fetchedTasks.forEach(taskId => {
			if (!configsData[configId][taskId])
				delete patchedSlaTimerData.config[configId][taskId];
		});
	});

	return patchedSlaTimerData;
};
