import {t} from 'sn-translate';

import {OUT_OF_SCHEDULE_STAGE_CONFIG} from '../../constants';

import {isPaused} from '../utils/stageUtils'; 

export default ({
	slaName,
	displayValue,
	stageValue,
	percentage,
	resumes,
	stops,
	breachedOn,
	durationRemaining,
	endTime
}) => {
	const messageParts = [];

	if (slaName)
		messageParts.push(slaName);

	if (isPaused(stageValue))
		messageParts.push(displayValue);

	if (breachedOn)
		messageParts.push(t('Breached on {0}', breachedOn));

	if (durationRemaining)
		messageParts.push(t('{0} remaining', durationRemaining));

	if (percentage || percentage === 0)
		messageParts.push(t('{0}% time elapsed', percentage));

	if (resumes)
		messageParts.push(t('{0} until {1}', displayValue, resumes));

	if (stops)
		messageParts.push(t('{0} on {1}', OUT_OF_SCHEDULE_STAGE_CONFIG.displayValue, stops));

	if (endTime)
		messageParts.push(t('{0} on {1}', displayValue, endTime));
  
	return messageParts.join(', ');
};
