
/* istanbul ignore file */
import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import debounce from 'lodash.debounce';

import {SLA_TIMER_GET_DATA_EVENT} from '../constants';

import {
	PROVIDER_FETCH_ACTION,
	PROVIDER_FETCH_DATA_ACTION,
	PROVIDER_FETCH_DATA_SUCCESS_ACTION,
	PROVIDER_RW_UPDATE_ACTION,
	PROVIDER_RW_MESSAGE_RECIEVED_ACTION,
	PROVIDER_RW_FETCH_ACTION
} from './constants';
import {
	handleTimerGetDataEvent,
	fetchData,
	httpCall,
	handleDataFetchSuccess,
	recordWatcher,
	fetchRwData
} from './effects';

createCustomElement('now-sla-timer-provider', {
	renderer: {
		type: snabbdom
	},
	properties: {
		slaTimerData: {
			default: {
				sla_timers: {},
				config: {}
			},
			selectable: true
		}
	},
	initialState: {
		tasks: new Set(),
		configurations: new Set(),
		tasksToFetch: new Set()
	},
	eventHandlers: [{
		events: [SLA_TIMER_GET_DATA_EVENT],
		effect: handleTimerGetDataEvent
	}],
	actionHandlers: {
		[PROVIDER_FETCH_ACTION]: debounce(fetchData, 100), 
		[PROVIDER_FETCH_DATA_ACTION]: httpCall,
		[PROVIDER_FETCH_DATA_SUCCESS_ACTION]: handleDataFetchSuccess,
		[PROVIDER_RW_UPDATE_ACTION]: recordWatcher,
		[PROVIDER_RW_MESSAGE_RECIEVED_ACTION]: fetchRwData,
		[PROVIDER_RW_FETCH_ACTION]: debounce(fetchData, 200)
	},
	dispatches: {
		[PROVIDER_FETCH_ACTION]: {private: true},
		[PROVIDER_FETCH_DATA_ACTION]: {private: true},
		[PROVIDER_FETCH_DATA_SUCCESS_ACTION]: {private: true},
		[PROVIDER_RW_UPDATE_ACTION]: {private: true},
		[PROVIDER_RW_MESSAGE_RECIEVED_ACTION]: {private: true},
		[PROVIDER_RW_FETCH_ACTION]: {private: true}
	}
});
