import getDisplayConfig from './getDisplayConfig';
import {inProgress} from './stageUtils';

import {SLA_TIMER_REFRESH_ACTION} from '../../constants';

export default ({
	state: {
		counter,
		scheduleTimeout,
		properties: {data}
	},
	updateState,
	dispatch
}) => {
	const timerData = typeof data === 'string' ? JSON.parse(data) : data;
	const {
		duration: {value: duration},
		time_left: {value: durationLeft},
		sla: {display_value: slaName},
		stage: {
			value: stageValue,
			display_value: stageDisplayValue
		},
		has_breached: {value: hasBreached},
		breach_time,
		schedule,
		end_time: {display_value: endTime}
	} = timerData;

	// Stop previous counter
	if (counter)
		clearInterval(counter);

	// Stop previous schedule timeout
	if (scheduleTimeout)
		clearTimeout(scheduleTimeout);

	let durationSecs = Math.floor(durationLeft / 1000);
	const fullDuration = Math.floor(duration / 1000);

	const breachedOn = breach_time && breach_time.display_value;

	const getDisplayConfigProps = {
		slaName,
		fullDuration,
		duration: durationSecs,
		hasBreached,
		breachedOn,
		stageDisplayValue,
		stageValue,
		schedule,
		endTime
	};

	// Create new counter and schedule timeout if SLA is in progress
	let newCounter = null;
	let newScheduleTimeout = null;
	if (inProgress(stageValue) && !hasBreached) {
		if (schedule && schedule.schedule_changes_millis) {
			newScheduleTimeout = setTimeout(() => {
				dispatch(SLA_TIMER_REFRESH_ACTION);
				clearInterval(newCounter);
				updateState({counter: null});
			}, schedule.schedule_changes_millis);
		}

		if (!schedule || (schedule && schedule.in_schedule)) {
			newCounter = setInterval(() => {
				durationSecs = durationSecs - 1;
		
				const timerDisplayConfig = getDisplayConfig({...getDisplayConfigProps, duration: durationSecs});
		
				if (durationSecs === 0) {
					if (newScheduleTimeout) {
						clearTimeout(newScheduleTimeout);
						timerDisplayConfig.scheduleTimeout = null;
					}
	
					clearInterval(newCounter);
					timerDisplayConfig.counter = null;
	
					dispatch(SLA_TIMER_REFRESH_ACTION);
				}
		
				updateState(timerDisplayConfig);
			}, 1000);
		}
	}

	// Update state with initial setup
	const initialState = getDisplayConfig(getDisplayConfigProps);

	updateState({
		...initialState,
		counter: newCounter,
		scheduleTimeout: newScheduleTimeout
	});
};
