import {createHttpEffect} from '@servicenow/ui-effect-http';
import {createAmbSubscriptionEffect} from '@servicenow/ui-effect-amb';

import patchData from './utils/patchData';
import {
	PROVIDER_FETCH_ACTION,
	PROVIDER_FETCH_DATA_ACTION,
	PROVIDER_FETCH_DATA_SUCCESS_ACTION,
	PROVIDER_RW_UPDATE_ACTION,
	PROVIDER_RW_MESSAGE_RECIEVED_ACTION,
	PROVIDER_RW_FETCH_ACTION
} from './constants';

const createFilterString = tasks => {
	const tasksArray = Array.from(tasks);
	const filterString = btoa(`taskIN${tasksArray.join(',')}^EQ`);

	return filterString.replace(/=/g, '-');
};

const updateRw = (tasks, subscribe, dispatch) => {
	dispatch(PROVIDER_RW_UPDATE_ACTION, {
		table: 'task_sla',
		filter: createFilterString(tasks),
		subscribe
	});
};

export const handleTimerGetDataEvent = ({
	action: {payload: {event}},
	state: {tasks, configurations, tasksToFetch},
	dispatch
}) => {
	const {detail: {task, configuration}} = event;

	if (tasks.size > 0)
		updateRw(tasks, false, dispatch);

	if (!tasks.has(task)) {
		tasks.add(task);
		updateRw(tasks, true, dispatch);
	}

	if (typeof configuration === 'string' && configuration.trim().length > 0)
		configurations.add(configuration);

	tasksToFetch.add(task);

	dispatch(PROVIDER_FETCH_ACTION);
};

export const fetchData = ({state: {configurations, tasksToFetch}, dispatch}) => {
	dispatch(PROVIDER_FETCH_DATA_ACTION, {
		body: {
			task_ids: Array.from(tasksToFetch),
			config_ids: Array.from(configurations)
		}
	});
};

export const httpCall = createHttpEffect('/api/sn_slm_timer/sla/timer', {
	method: 'POST',
	dataParam: 'body',
	successActionType: PROVIDER_FETCH_DATA_SUCCESS_ACTION,
	batch: false
});

export const handleDataFetchSuccess = ({
	action: {
		payload: {result}
	},
	state: {properties, tasksToFetch},
	updateProperties,
	updateState
}) => {
	const props = properties ? properties : {};
	const slaTimerData = result && patchData(props.slaTimerData, result, tasksToFetch);

	if (slaTimerData)
		updateProperties({slaTimerData});

	updateState({tasksToFetch: new Set()});
};

export const recordWatcher = createAmbSubscriptionEffect('/rw/default/:table/:filter', {
	messageReceivedActionType: PROVIDER_RW_MESSAGE_RECIEVED_ACTION,
});

export const fetchRwData = ({
	properties: {slaTimerData},
	state: {tasksToFetch},
	action: {
		payload: {data: {sys_id: taskSlaSysId, record, changes, operation}}
	},
	dispatch
}) => {
	let taskSysId = record && record.task && record.task.value;
	if (!taskSysId) {
		const timerData = slaTimerData && slaTimerData.sla_timers && slaTimerData.sla_timers[taskSlaSysId];
		taskSysId = timerData && timerData.task && timerData.task.value;
	}

	const isRelevantOperation = operation && (operation === 'insert' || operation === 'delete');
	const isRelevantUpdate = Array.isArray(changes) && (changes.includes('stage') || changes.includes('has_breached'));
	if (taskSysId && (isRelevantUpdate || isRelevantOperation)) {
		tasksToFetch.add(taskSysId);
		dispatch(PROVIDER_RW_FETCH_ACTION);
	}
};
