export default (stageValue, percentage) => {
	if (stageValue === 'breached') {
		return {
			variant: 'primary',
			color: 'critical'
		};
	}

	if (percentage || percentage === 0) {
		let color;
		
		if (percentage < 50)
			color = 'positive';
		else
			color = percentage < 75 ? 'warning' : 'high';

		return {
			variant: 'secondary',
			color
		};
	}

	return {
		variant: 'secondary',
		color: 'gray'
	};
};
